
import { defineComponent } from 'vue'
import { CountryApi, Country } from '../Models/CountryModels'
import { Form } from '../Models/FormModel'

export default defineComponent({
  name: 'Form',
  data: () => {
    return {
      countryList: Array<Country>(),
      cityList: Array<string>(),
      vaseId: '0',
      selectedCountry: '',
      formText: '',
      selectedCity: '',
      isCityVisible: false
    }
  },
  created () {
    this.loadCountries()
  },
  methods: {
    async loadCountries () {
      const response = await fetch('https://countriesnow.space/api/v0.1/countries')
      const json = await response.json()
      json.data.forEach((countryApi: CountryApi) => {
        const country = new Country(countryApi.iso2, countryApi.country, countryApi.cities)
        this.countryList.push(country)
      })
    },
    countryChanged () {
      const country = this.countryList.filter((country : Country) => {
        if (country.code === this.selectedCountry) {
          return true
        }
      })
      if (country.length > 0) {
        this.cityList = country[0].cities
        this.isCityVisible = true
      } else {
        this.isCityVisible = false
      }
    },
    async submit () {
      const country = this.findCountry()
      const countryCode = (country) ? country.code : ''
      const countryName = (country) ? country.name : ''
      const form = new Form(this.vaseId, countryName, this.selectedCity, countryCode, this.formText)
      const response = await fetch('http://localhost:3000/api/form', {
        mode: 'cors',
        method: 'post',
        body: JSON.stringify(form)
      })
      console.log(response)
    },
    findCountry () {
      const country = this.countryList.filter((country: Country) => {
        if (country.code === this.selectedCountry) {
          return true
        }
      })
      if (country.length > 0) {
        return country[0]
      } else {
        return false
      }
    }
  }
})
