export class Form {
    vaseId: string;
    country: string;
    city: string;
    countryCode: string;
    text: string;

    constructor (vaseId: string, country: string, city: string, countryCode: string, text: string) {
      this.vaseId = vaseId
      this.country = country
      this.city = city
      this.countryCode = countryCode
      this.text = text
    }
}
